export default{
    name:"searchbar",
    props:{
        serial:"",
        clicksearch:Function,
        data:Object,
        titledata:"",
    },
    data(){
        return{
            boxvalue:"",
        }
    },
    methods:{
        showserial(){
            $(".barSeriation").toggle();
            $("#zhezhao").toggle();
        },
        clicksea(e,name,index){
            for(let i=0,data=$(".seriationImg").length;i<data;i++){
                $(".seriationImg").eq(i).hide();
            }
            $(".seriationImg").eq(index).show();
            if(name=="全部"){
                this.data.filter="";
            }else{
            this.data.filter=name;
            }
            this.clisearch();
            this.showserial();
        },
        clisearch(e){
            // console.log(e,this.boxvalue);
            if(e==undefined){
               this.data.param=this.boxvalue
            }else{
            this.data.param=e;
            this.boxvalue=e
            }
             this.clicksearch();
        },
        batchFlicking(){
            this.saoma(this.clisearch);
        }
    },
    mounted() {
        $(".seriationImg").eq(0).show();
    },
}



